



























export default {
	data() {
		return {
			uploadimg:'',
			percent:0,
			tasksDetails:{
				taskTitle:'',
				copyWriting:'',
				picId:'',
				picId2:'',
				taskIdentifier:"",//任务编号
				statusTask:''//任务状态
			},
			orderInfo:{
				taskIdentifier:'',
				account:'',
				orderNo:''
			},
			order:{}
			
		};
	},
	onLoad(options){
		 let order = JSON.parse(options.task);
		 this.tasksDetails.taskTitle = order.taskInfo.taskTitle;
		 this.orderInfo.taskIdentifier = order.taskInfo.taskIdentifier; //编号
		 this.orderInfo.orderNo = order.orderNo;
		 this.orderInfo.account = uni.getStorageSync('userInfo').account;
	},
	methods:{
		chooseImg() {
			var that = this;
			//选择图片
			uni.chooseImage({
				sourceType: ['camera', 'album'],
				sizeType: 'compressed',
				count: 1,
				success: function(res) { 
					const tempFilePaths = res.tempFilePaths;
					const uploadTask = uni.uploadFile({
						url: that.uploadUrl,
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {},
						success: (uploadFileRes) => {
							that.uploadimg = tempFilePaths[0];
						}
					});
					uploadTask.onProgressUpdate(function(res){
						that.percent = res.progress;
					})
					
				}
			});
			},
		previewImage(){
			//预览图片
			uni.previewImage({
				urls: [this.uploadimg]
			});
		},
		removeImage(img) {
			var that = this;
			uni.showModal({
				content: '确认删除选中的图片？',
				success: function() {
					if(res.confirm){
						that.uploadimg = '';

					}	
					if(res.cancel){
					
					}
					
				}
			});
		},
		getTask(){
			const jsonString = {
				orderInfo:this.orderInfo,
				requestType:"finishOrde",
			}
			const param ={
				controllerRequestType:"orderControllerService",
				jsonString:JSON.stringify(jsonString)
			}
			uni.request({
				url:this.websiteUrl,
				method:'POST',
				header: {
					"content-type":"application/x-www-form-urlencoded"
				},
				data:param,
				success(res) {
					console.log(res.data);
					if(res.data.errorCode =='0000'){
						uni.showModal({
							content:'您已经提交任务,请等待管理员审核',
							
							success(res) {
								if(res.confirm){
									uni.reLaunch({
											url:'../../main/main',

									});
								}
									
								if(res.cancel){
									uni.reLaunch({
											url:'../../main/main',
									});
								}
							}
						})
						
					}else{
						uni.showToast({
							icon:'none',
							title:res.data.errorMessage
						})
					}
				},
				fail(res) {
				uni.showToast({
					icon:'none',
					title:'网络异常！请稍后重试'
				})	
				}
			})	
			
			
				
		}
		
	}		
}



